
import XHeader from 'vux/src/components/x-header/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import { validateNotNull, validatePhone } from 'util/rules'
import localStore from 'util/localStore'

export default {
data () {
return {
msg1: '',
img1: require('../../assets/images/fuhe_ic.png'),
img2: require('../../assets/images/bufuhe_ic.png'),
listArr: [],
addressData: '',
value: [],
showAddress: false,
hasId: false,
til: '客户建档',
formData: {
phoneNo: '',
name: '',
sex: 0,
province: '请选择',
id: '',
city: '',
district: '',
address: '',
level: '',
remark: ''
},
addressName: [],
inited: false, // 防止狂点
hasData: false,
hasFocus: false,
region: [],
provinceList: []
}
},
watch: {







'formData.phoneNo': {
handler (val, oldVal) {
if (this.formData.phoneNo.length === 11 && this.hasFocus) {
this.ifExist(oldVal)
}
}
}
},
methods: {
onHide (type) {
if (type) {
let address = this.$refs.popupPicker.getNameValues().split(' ')
this.formData.province = address[0]
this.formData.city = address[1]
this.formData.district = address[2]
}
},
goBack () {
if (this.hasFocus) {
let thiz = this
this.$vux.confirm.show({
title: '操作提示',
content: '是否确定离开本页面？',
onCancel () {},
onConfirm () {
thiz.$router.go(-1)
}
})
} else {
this.$router.go(-1)
}
},
ifExist (oldVal) {
this.$tupHttp(this, 'client-biz/client-biz/storeClient/ifExist?phoneNo=' + this.formData.phoneNo, 'GET').then(res => {
if (this.formData.id && res.data.model && res.data.model !== this.formData.id) {
this.hasData = true
this.$vux.toast.text('该号码已有客户档案')
} else if (!this.formData.id && res.data.model) {
this.hasData = true
this.$vux.toast.text('该号码已有客户档案，系统将自动为您跳转')
setTimeout(() => {
this.hasFocus = false
this.$router.push({
path: '/customerSRV',
query: {
id: res.data.model
}
})
}, 500)
} else {
this.hasData = false
}
})
},
onShadowChange (ids, names) {
this.addressName = names
},
_getInfo (id) {
this.$tupHttp(this, 'client-biz/client-biz/storeClient/clientDetail', 'POST', {clientInfoId: id}).then(res => {
this.formData = res.data.model
let province = ''
let city = ''
let area = ''
this.provinceList.forEach(item => {
if (item.name === this.formData.province) {
province = item.value
}
if (item.name === this.formData.city) {
city = item.value
}
if (item.name === this.formData.district) {
area = item.value
}
})
this.region = [province, city, area]
})
},
toSRV () {
if (this.hasData) {
this.$vux.toast.text('该号码已有客户档案')
return
}
if (this.inited) {
this.$vux.toast.text('拼命请求中，请稍候~')
return
}
if (validatePhone(this, this.formData.phoneNo) && validateNotNull(this, this.formData.name, '姓名') && validateNotNull(this, this.formData.city, '省市区') && validateNotNull(this, this.formData.address, '详细地址')) {
this.inited = true
this.$tupHttp(this, this.formData.id ? 'client-biz/client-biz/storeClient/updateClient' : 'client-biz/client-biz/storeClient/saveClient', 'POST', this.formData).then(res => {
this.$vux.toast.text('保存成功！')
setTimeout(() => {
this.hasFocus = false
this.$router.push({
path: '/customerSRV',
query: {
id: this.formData.id ? this.formData.id : res.data.model.id
}
})
}, 500)
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
}
}
},
created () {
this.hasId = !!this.$route.query.id
if (this.$route.query.phoneNo) {
this.formData.phoneNo = this.$route.query.phoneNo
}
this.$tupHttp(this, 'https://tup-templates.oss-cn-hangzhou.aliyuncs.com/province.json').then(res => {
this.provinceList = res.data.models
if (this.hasId) {
this._getInfo(this.$route.query.id)
} else if (localStore.get('TUP_pActiveProvince')) {
let province = ''
let city = ''
let area = ''
this.provinceList.forEach(item => {
if (item.name === localStore.get('TUP_pActiveProvince')) {
province = item.value
}
if (item.name === localStore.get('TUP_pActiveCity')) {
city = item.value
}
if (item.name === localStore.get('TUP_pActiveArea')) {
area = item.value
}
})
this.region = [province, city, area]
this.formData.province = localStore.get('TUP_pActiveProvince')
this.formData.city = localStore.get('TUP_pActiveCity')
this.formData.district = localStore.get('TUP_pActiveArea')
}
})
},
components: {
XHeader,
Checker,
CheckerItem,
XTextarea,
XAddress,
PopupPicker
}
}



